<template>
  <Navbar 
  :header="header" 
  @sendSearch="search = $event"
  />
  <Notes 
  :search="search" 
  :notes="filterNotes" 
  @delNote="delNote"
  @changeNote="changeNote"
  />
  <Modal 
  v-show="modalOpen" 
  @closeModal="closeModal" 
  @addNote="addNote"
  :edit="edit"
  :editNote="editNote"
  @editedNote="editedNote"
  />
  <AddButton @openModal="openModal"/>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
import AddButton from "@/components/AddButton.vue";
export default {
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton,
  },
  data() {
    return {
      modalOpen: false,
      header: true,
      search: "",
      notes: [],
      edit: false,
      editNote: {}
    };
  },
  computed: {
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().trim().includes(this.search.toLowerCase().trim())) : this.notes;
    }
  },
  watch: {
    notes: {
      handler(updatedNotes){
        localStorage.notes = JSON.stringify(updatedNotes);
      },
      deep: true
    }
  },
  created(){
    this.getNotes();
  },
  methods: {
    openModal(){
      this.modalOpen = true;
      this.edit = false;
    },
    closeModal(){
      this.modalOpen = false;
    },
    addNote(note){
      this.notes.push(note);
      this.modalOpen = false;
    },
    delNote(id){
      this.notes = this.notes.filter(note => note.id != id);
    },
    getNotes(){
      let localNotes = localStorage.notes
      if(localNotes){
        this.notes = JSON.parse(localNotes);
      }
    },
    changeNote(id){
      this.modalOpen = this.edit = true
      this.editNote = this.notes.find(note => note.id == id);
    },
    editedNote(newNote){
      this.notes.forEach(note => {
        if(note.id == newNote.id){
          note.title = newNote.title;
          note.desc = newNote.desc;
          note.date = newNote.date;
        }
      })
    }
  }
};
</script>

