<template>
  <header class="header">
    <Transition name="header__nav">
      <nav class="header__nav" v-show="header === true">
        <button class="header__lang" @click="switchLang">{{ $i18n.locale }}</button>
        <h1 class="header__title">{{$t('notes')}}</h1>
        <button class="header__serach" @click="header = false">
          <img src="@/assets/images/search.svg" alt="" />
        </button>
      </nav>
    </Transition>
    <Transition name="header__form">
    <nav class="header__form" v-show="header === false">
      <button class="header__back" @click="(header = true), (search = '')">
        <img src="@/assets/images/back.svg" alt="" />
      </button>
      <div class="container">
        <input
          v-model="search"
          type="text"
          class="header__input"
          :placeholder="$t('searchInput')"
        />
      </div>
      <button class="header__clear" @click="search = ''">
        <img src="@/assets/images/clear.svg" alt="" />
      </button>
    </nav>
    </Transition>
  </header>
</template>

<script>
export default {
  props: {
    header: Boolean,
  },
  data() {
    return {
      search: "",
    };
  },
  methods: {
    switchLang(){
      this.$i18n.locale == 'ru' ? this.$i18n.locale = 'uz' : this.$i18n.locale = 'ru'
      localStorage.lang = this.$i18n.locale
    }
  },
  watch: {
    search(newSearch) {
      this.$emit("sendSearch", newSearch);
    },
  },
};
</script>

<style>
.header {
  background: #f3edf7;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.3),
    0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 14px 16px;
  height: 64px;
  overflow: hidden;
}

.header__nav,
.header__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__lang {
  font-family: 22px;
  font-weight: 700;
  text-transform: uppercase;
}

.header__title {
  color: #1c1b1f;
  text-align: center;
  font-size: 22px;
  font-weight: 400;
  line-height: 127%;
}

.header__input {
  display: block;
  width: 100%;
  color: #9d9d9d;
  font-size: 16px;
  font-weight: 400;
  line-height: 125%;
}

.header__nav-enter-active, .header__nav-leave-active {
  transition: 500ms linear;
}

.header__nav-enter-from, .header__nav-leave-to {
  transform: translateY(-200px);
  opacity: 0;
}

.header__form-enter-active, .header__form-leave-active {
  transition: 600ms linear;
}

.header__form-enter-from, .header__form-leave-to {
  transform: translateY(200px);
  opacity: 0;
}
</style>