<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__title">
          {{ !edit ? $t('addNote') : $t('changeNote') }}
        </h2>
        <div class="madal__inputs">
          <label>
            <span>Title</span>
            <input v-model="title" type="text" placeholder="Title" />
          </label>
          <label>
            <span>Content</span>
            <textarea v-model="desc" placeholder="Content"></textarea>
          </label>
        </div>
        <div class="modal__btns">
          <button class="modal__btn cancel" @click="closeModal">{{$t('cancel')}}</button>
          <button class="modal__btn add" @click="addNote" v-if="!edit">{{$t('add')}}</button>
          <button class="modal__btn add" @click="changeNote" v-else>{{$t('change')}}</button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from "uuid";

export default {
  props: {
    edit: Boolean,
    editNote: Object,
  },
  data() {
    return {
      title: "",
      desc: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.title = "";
      this.desc = "";
    },
    addNote() {
      if (this.title != "" && this.desc != "") {
        let newNote = {
          id: uuidv4(),
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", newNote);
        this.title = "";
        this.desc = "";
      }
    },
    changeNote() {
      if (this.title != "" && this.desc != "") {
        let editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editedNote", editedNote);
        this.closeModal();
      }
    },
  },
};
</script>

<style>
.modal {
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  inset: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__block {
  max-width: 312px;
  width: 100%;
  padding: 24px;
  border-radius: 28px;
  border-radius: 28px;
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11) 0%,
      rgba(103, 80, 164, 0.11) 100%
    ),
    #fffbfe;
}

.modal__title {
  color: rgb(28, 27, 31);
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
  margin-bottom: 16px;
}

.madal__inputs {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;
}

.madal__inputs label {
  position: relative;
}

.madal__inputs span {
  position: absolute;
  top: 8px;
  left: 16px;
  color: rgb(103, 80, 164);
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.4px;
}

.madal__inputs input,
.madal__inputs textarea {
  padding: 23px 16px 9px;
  width: 100%;
  height: 57px;
  border-radius: 4px 4px 0px 0px;
  background: rgb(231, 224, 236);
  color: rgb(73, 69, 79);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.5px;
  border-bottom: 1px solid rgb(28, 27, 31);
}

.modal__btns {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}

.modal__btn {
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.1px;
  text-align: center;
  text-transform: uppercase;
  border-radius: 5px;
  padding: 10px 12px;
  transition: 300ms linear;
}

.cancel {
  color: rgb(207, 27, 27);
}

.add {
  color: rgb(103, 80, 164);
}

.cancel:hover {
  background: rgb(255, 225, 225);
}

.add:hover {
  background: rgb(230, 221, 255);
}

.modal-enter-active,
.modal-leave-active {
  transition: 200ms linear;
}

.modal-enter-from,
.modal-leave-to {
  transform: scale(1.5);
  opacity: 0;
}
</style>